$appBackground: #f3f3f3;
$containerBackground: #ffffff;
$upgradesBackground: #cacaca;
$tooltipBackground: #e2e2e2;
$workersBackground: #fff2da;
$workerBackground: #f3f3f3;
$mineBlockBackground: #763d17;
$boughtUpgradeColor: #ffd702;
$notificationColor: #293239;

$containerBorders: 2px solid #515151;
$workersBorders: 3px solid #cbcbcb;

$mainFont: Arial, sans-serif;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app {
  position: relative;
  height: 785px;
  width: 1024px;
  background: $appBackground;
  margin: 0 auto;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: $containerBackground;
}

.left-panel {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: $containerBorders;
}

.title {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $mainFont;
  font-size: 22px;
  border-bottom: $containerBorders;
}

.mine-block {
  width: 100%;
  height: 500px;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  background: $mineBlockBackground;
}

.mine {
  width: 300px;
  height: 300px;
  background-image: url("./resources/images/mine.jpg");
  background-size: cover;
  background-position: center;
  border-radius: 100%;
  cursor: pointer;
  transition: all .1s ease-out;

  &:hover {
    transform: scale(1.1);
  }
}

.money-block {
  width: 100%;
  height: 40px;
  font: 16px $mainFont;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: pre;
}

.heat {

  &-panel {
    height: 90%;
    width: 85px;
    margin-top: 30px;
    background: darkgrey;
    border: $containerBorders;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-gauge {
    height: 95%;
    width: 30px;
    border: $containerBorders;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &-level {
    width: 100%;
    height: 0;
    background-color: #0ebfbf;
    transition: all ease-out .2s;
    border-top: 3px solid #10a4a4;
  }
}

.right-panel {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: $containerBorders;
}

.upgrades {
  width: 100%;
  display: flex;
  flex-direction: column;
  background: $upgradesBackground;

  &-title {
    height: 30px;
    width: 100%;
    font: 18px $mainFont;
    padding: 3px;
  }

  &-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 200px;
    width: 100%;
    border-top: $containerBorders;
    border-bottom: $containerBorders;
  }
}

.upgrade {
  width: 70px;
  height: 70px;
  background-size: cover;
  background-position: center;
  background-color: rgba(white, 0.5);
  background-repeat: no-repeat;
  position: relative;
  border: 2px solid rgba(black, 0.2);
  cursor: pointer;

  &--bought {
    border-color: $boughtUpgradeColor;
  }

  .tooltip {
    position: absolute;
    width: 200px;
    border: $containerBorders;
    border-radius: 3px;
    display: none;
    flex-direction: column;
    background: $tooltipBackground;
    top: 110%;
    left: 110%;
    padding: 5px;

    &-title {
      font: bold 20px $mainFont;
      min-height: 20px;
      width: 100%;
    }

    &-price {
      font: 18px $mainFont;
      min-height: 20px;
      width: 100%;
    }

    &-description {
      font: 16px $mainFont;
      width: 100%;
    }
  }

  &:hover {
    .tooltip {
      display: flex;
    }
  }
}

.workers {
  width: 100%;
  display: flex;
  overflow-y: auto;
  flex-direction: column;
  background: $workersBackground;

  &-title {
    height: 30px;
    width: 100%;
    font: 18px $mainFont;
    padding: 3px;
  }

  &-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: $containerBorders;
    border-bottom: $containerBorders;
  }
}

.worker {
  display: flex;
  margin: 6px auto;
  width: 100%;
  height: 90px;
  align-items: center;
  justify-content: space-around;
  padding: 3px;
  cursor: pointer;
  border: $workersBorders;
  background: $workerBackground;

  &-image {
    height: 65px;
    width: 65px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  &-name {
    font: 20px $mainFont;
  }

  &-price {
    font: 16px $mainFont;
  }

  &-quantity {
    font: 22px $mainFont;
  }
}

.inc-text {
  position: absolute;
  pointer-events: none;
  font: 22px $mainFont;
  color: white;
  opacity: 1;
  transition: all 3s;

  &--flying-up {
    transform: translate(0, -100px);
    font-size: 10px;
    opacity: 0;
  }
}

.dust-particle {
  position: absolute;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  opacity: 0.6;
  pointer-events: none;
  background-size: cover;
  background-position: center;
}

.notifications-container {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 300px;
  padding: 10px;
  bottom: 5%;
  left: 5%;
  background: rgba(255,255,255, 0.2);
  border-radius: 10px;
  pointer-events: none;
}

.notification {
  display: block;
  width: 100%;
  padding: 3px;
  font: 14px $mainFont;
  color: $notificationColor;
}

.fancy-notification {

  &-back {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  &-container {
    display: flex;
    position: relative;
    width: 80%;
    flex-direction: column;

    &--animated {
      animation: fancy-notify 3s ease-in-out;
      animation-iteration-count: 1;

      .fancy-notification-text {
        animation: letters-wide 3s linear;
        animation-iteration-count: 1;

        @keyframes letters-wide {
          from {letter-spacing: 1px; transform: scale(1)}
          to {letter-spacing: 10px; transform: scale(1.5)}
        }
      }
    }

    @keyframes fancy-notify {
      0% {transform: scale(1);}
      10% {transform: scale(0.8)}
      90% {transform: scale(0.6)}
      100% {transform: scale(0.01)}
    }
  }

  &-shine-1 {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("./resources/images/effects/shine.png");
    animation: shining-1 5s linear infinite;
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: center;
    opacity: 0.6;
    z-index: 1;

    @keyframes shining-1 {
      0% {transform: rotate(0deg);}
      100% {transform: rotate(360deg);}
    }
  }

  &-shine-2 {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("./resources/images/effects/shine.png");
    animation: shining-2 4s linear infinite;
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: center;
    opacity: 0.8;
    z-index: 1;

    @keyframes shining-2 {
      0% {transform: rotate(-10deg);}
      100% {transform: rotate(350deg);}
    }
  }

  &-image {
    width: 100%;
    height: 500px;
    margin-bottom: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    animation: move-around 10s ease-in-out infinite;
    z-index: 2;

    @keyframes move-around {
      0% {transform: translate(0, 0)}
      20% {transform: translate(50px, 50px) rotate(10deg)}
      40% {transform: translate(40px, -40px)}
      60% {transform: translate(0px, -40px)}
      80% {transform: translate(-20px, -50px) rotate(-10deg)}
      100% {transform: translate(0, 0)}
    }
  }

  &-text {
    font: 60px $mainFont;
    color: black;
    width: 100%;
    text-align: center;
  }
}

.boost-notification {

  &-back {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  &-container {
    display: flex;
    position: relative;
    width: 80%;
    flex-direction: column;

    &--animated {
      animation: fancy-notify 2s ease-in-out;
      animation-iteration-count: 1;

      .fancy-notification-text {
        animation: letters-wide 3s linear;
        animation-iteration-count: 1;

        @keyframes letters-wide {
          from {letter-spacing: 1px; transform: scale(1)}
          to {letter-spacing: 10px; transform: scale(1.5)}
        }
      }
    }

